@import '/src/styles/index.scss';
.filter-section {
    display: flex;
    flex-direction: column;
    width: 225px;
    padding: 16px;
}

.filter-category-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.filter-body {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    flex: 1;
}
.filter-body-row1>span {
    border-radius: 2px;
    background: var(--primary-6, #1890FF);
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    padding: 3px 10px;
    width: fit-content;
    color: var(--character-primary-inverse, #FFF);
    text-align: center;
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    // line-height: 22px;
}
.filter-body-row1 {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 32px;
}

.divider {
    border-top: 1px solid rgba(0,0,0,0.06);
    opacity: 1;    
    flex: 1;
    height: 0;
}
.filter-section span.label{
    color: $character-secondary-45;
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    margin-bottom: 8px;
}

.filter-item {
    border: 1px solid $primary-6 ;
    color: $primary-6;
    padding: 0 15px;
    margin-bottom: 8px;
    height: 32px;
    line-height: 32px;
}
.filter-item.red{
    border: 1px solid $dust-red-6;
    color: $dust-red-6;
}