@import '/src/styles/index.scss';

.dashboard-info {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding-right: 16px;
}
.dashboard-info-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;    
}
.dashboard-info-header-filter {
    display: flex;
    gap: 16px;
    align-items: center;
    justify-content: flex-start;
}
.dashboard-info-header-viewmode {
    display: flex;
    justify-content: space-evenly;
    gap: 5px;
}
.dashboard-info span.label {
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    color: $character-secondary-45;
    margin-left: 5px;
}
.dashboard-info span.value {
    color: $primary-6;
    /* Body/regular */
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
}
.dashboard-info span.value.black {
    color: $character-title-85;
}
.dashboard-info span.summary {
    margin-top: 8px;
    color: $character-secondary-45;
    font-size: 16px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    margin-left: 5px;
}

.dashboard-info a.source-id-link {
    margin-left: 5px;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 2px;
    background-color: $primary-6;
    color: $character-primary-inverse;
    text-align: center;
    padding: 0 6px;
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    text-decoration: none;
}
.dashboard-info a.source-id-link.red {
    background-color: $dust-red-6;
}
.dashboard-info a.button {
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 2px;
    background-color: $primary-6;
    color: $character-primary-inverse;
    text-align: center;
    padding: 0 15px;
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    text-decoration: none;
    height: 32px;
    line-height: 32px;
    width: 103px;
    margin-top: 16px;
}
.dashboard-info-body {
    display: flex;
    flex-direction: column;
    flex: 1;
    gap: 8px;
}