@import '/src/styles/index.scss';

::-webkit-scrollbar {
    display: none;
}
.navigation-wrapper {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    scroll-behavior: smooth;
    height : 100%;
    width: 238px;
    background-color: white;
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.15);
    // border-right: 1px solid black;
}
.case-select {
    width: 100%;
    margin-bottom: 10px;
    border : 1px solid #D9D9D9;
    border-radius: 2px;
    padding: 3px;
}
.case-select span {
    color: $secondary;
}

.case-select-wrapper {
    margin: 10px;
    display: flex;
    flex-direction: column;
}
.case-select-wrapper>span {
    margin-bottom:5px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: rgba(0, 0, 0, 0.85);
}
