@import '/src/styles/index.scss';

.dashboard-header {
    display: flex;
    justify-content: space-between;
    height: 64px;
    align-items: center;
    padding: 0 16px;
}

.dashboard-header .dashboard-title {
    font-size: 16px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    color: $secondary;
}
.header-menu-wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 12px;
}

.img-avatar {
    width: 24px;
    height: 24px;
}
.avatar-wrapper {
    margin: 0 12px;
    display: flex;
    align-items: center;
    gap: 8px;
}
.avatar-wrapper span {
    font-size: 14px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;    
    color: $character-title-85;
}